input[type=text].forms-input-text {
  padding: 10px 9px;
  border-radius: 1px;
  box-sizing: border-box;
  border: 0.5px solid #777777;
  outline: none; }

.divisor-vertical {
  border-left: 0.5px solid #777;
  height: 100px;
  position: absolute; }

div.location-item {
  flex: 1;
  flex-direction: row;
  display: flex;
  width: 100%; }

div.location-card-wrapper {
  flex: 1;
  flex-direction: column;
  display: flex;
  justify-content: center;
  align-items: center;
  position: absolute;
  top: 150px; }
  div.location-card-wrapper h2 {
    font-size: 3.2rem;
    font-family: 'Roboto';
    color: #fff;
    font-weight: bold;
    line-height: 30px;
    margin: 20px; }

div.locationimage {
  flex: 1;
  flex-direction: column;
  display: flex;
  width: 100%;
  justify-content: center;
  align-items: center; }
  div.locationimage img {
    width: 100%;
    height: 85vh; }
  div.locationimage div.location-card {
    flex-direction: column;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 90px;
    width: 195px;
    background-color: #ffffff;
    border-radius: 3px;
    padding: 5px 6px; }
    div.locationimage div.location-card p {
      font-size: 12px;
      font-family: 'Roboto';
      color: #777;
      font-weight: 400;
      line-height: 16px;
      margin: 0; }
    div.locationimage div.location-card .locations-content-street {
      color: #000;
      font-weight: 600;
      line-height: 30px; }
    div.locationimage div.location-card .locations-content-phone {
      color: #f47320;
      font-weight: 600;
      line-height: 30px; }

a:visited {
  text-decoration: none; }

a:active {
  text-decoration: none; }

a:link {
  text-decoration: none; }
