.favoriteplaces-wrapper {
  flex: 1;
  flex-direction: column;
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 200px;
  width: 100%;
  background-color: white; }
  .favoriteplaces-wrapper .favoriteplaces-cities {
    margin: 20px 0; }
    .favoriteplaces-wrapper .favoriteplaces-cities span {
      font-size: 0.5rem;
      font-weight: 200;
      color: #777777;
      margin: 0 20px; }

.favoriteplaces-head {
  flex: 1;
  flex-direction: row;
  display: flex;
  justify-content: center;
  align-items: center; }
  .favoriteplaces-head .favoriteplaces-selector {
    margin: 0 5px;
    position: relative;
    font-family: Arial; }
  .favoriteplaces-head .favoriteplaces-selector select {
    background-color: #fff;
    padding: 5px;
    border: 1px solid transparent;
    border-color: rgba(0, 0, 0, 0.1);
    cursor: pointer;
    user-select: none;
    outline: none; }

.favoriteplace-item-wrapper-right {
  flex: 1;
  flex-direction: column;
  display: flex;
  justify-content: center;
  align-items: flex-end;
  align-content: flex-end;
  max-width: 140px; }
  .favoriteplace-item-wrapper-right :nth-child(2) {
    color: #bebebe; }
  .favoriteplace-item-wrapper-right span {
    font-size: 0.7rem;
    font-weight: 400;
    color: #777777;
    text-align: end;
    line-height: 15px; }

.favoriteplace-item-head {
  flex: 1;
  flex-direction: row;
  display: flex;
  justify-content: center;
  align-items: center; }
  .favoriteplace-item-head p {
    font-size: 0.75rem;
    font-weight: 600;
    color: #000;
    text-align: end;
    margin: 0;
    line-height: 20px; }
  .favoriteplace-item-head #favoriteplace-item-icon {
    width: 33px;
    height: 33px;
    margin: 5px;
    background: url("/static/images/icons/icons.png") -99px 0; }
