.head-form {
  flex: 1;
  flex-direction: column;
  display: flex;
  justify-content: flex-end;
  align-items: center;
  min-height: 280px;
  width: 100%;
  background-color: #f47320; }
  .head-form p, .head-form h2 {
    font-family: 'Roboto', sans-serif;
    font-weight: 500;
    color: white; }
  .head-form p {
    font-size: 1.2rem; }
  .head-form h2 {
    font-size: 2.6rem; }

.form-content {
  flex: 1;
  flex-direction: column;
  display: flex;
  justify-content: center;
  min-height: 400px;
  width: 100%;
  background-color: white; }

.fiels-wrapper {
  flex: 1;
  flex-direction: column;
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 20px 0; }
  .fiels-wrapper .form-steps-wrapper {
    flex: 1;
    flex-direction: row;
    display: flex;
    justify-content: flex-start;
    align-items: center; }

.tellus-wrapper {
  flex: 1;
  flex-direction: column;
  display: flex;
  justify-content: flex-end; }
  .tellus-wrapper textarea {
    width: 430px;
    height: 170px;
    max-width: 430px;
    max-height: 170px;
    min-width: 430px;
    min-height: 170px;
    margin: 0px;
    padding: 10px;
    box-sizing: border-box;
    border: 0.5px solid #777777;
    outline: none;
    resize: none; }
  .tellus-wrapper .tellus-button-wrapper {
    flex: 1;
    flex-direction: row;
    display: flex;
    justify-content: flex-end;
    align-items: flex-end; }

.form-button {
  background-color: #f47320;
  border: 0;
  margin: 20px 0 0 0;
  padding: 5px 10px;
  border-radius: 1px;
  cursor: pointer; }
  .form-button span {
    color: white;
    font-size: 0.75rem;
    font-family: 'Montserrat', sans-serif;
    font-weight: 600; }

.form-steps {
  flex-direction: column;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 16px;
  width: 16px;
  border: 0.5px solid #f47320;
  background-color: #f47320;
  border-radius: 8px;
  text-align: center;
  vertical-align: middle;
  margin: 5px; }
  .form-steps span {
    color: #ffffff;
    font-size: 0.75rem;
    font-family: 'Montserrat', sans-serif;
    font-weight: 600; }

.form-input-wrapper {
  flex: 1;
  flex-direction: column;
  display: flex;
  margin: 2px 10px;
  height: 50px; }
  .form-input-wrapper input {
    width: 190px; }
  .form-input-wrapper span {
    font-size: 0.9rem;
    font-weight: 400;
    color: #f47320;
    line-height: 15px;
    margin: 0; }
